<template>
  <div class="login">
    <div class="shading">
      <div class="pictrue">
        <img src="../assets/logo2.png" alt="" />
      </div>
    </div>
    <van-tabs class="whiteBg" v-show="ifshowing">
      <van-tab title="账号登录">
        <van-form @submit="doLogin">
          <van-field
            v-model="account"
            right-icon="phone-o"
            name="account"
            placeholder="请填写手机号"
            :rules="[
              { required: true, message: '请填写用户名' },
              { pattern: /^1[345678]\d{9}$/, message: '手机号码格式错误' },
            ]"
          />
          <van-field
            v-model="password"
            right-icon="lock"
            type="password"
            name="password"
            placeholder="请填写密码"
            :rules="[
              { required: true, message: '请填写密码' },
              { pattern: /^\w{5,18}$/, message: '密码由5-18数字a-zA-Z组成' },
            ]"
          />
          <div class="buttonsub">
            <van-button round block type="info" native-type="submit"
              >登录</van-button
            >
          </div>
        </van-form>
        <div class="registershow">
          <span>没有账号?</span
          ><span class="registerspan" @click="ifshowing = !ifshowing"
            >立即注册</span
          >
        </div>
      </van-tab>
      <van-tab title="快速登录">
        <van-form @submit="doSmsLogin">
          <van-field
            v-model="mobile"
            left-icon="phone-o"
            name="mobile"
            placeholder="请填写手机号"
            :rules="[
              { required: true, message: '请填写手机号' },
              { pattern: /^1[345678]\d{9}$/, message: '手机号码格式错误' },
            ]"
          />
          <van-field
            v-model="sms"
            name="sms"
            left-icon="lock"
            placeholder="请输入验证码"
            :rules="[{ required: true, message: '请填写验证码' }]"
          >
            <template #button>
              <van-button
                round
                size="small"
                :disabled="countdown < 60"
                @click.stop="getSms('login')"
                native-type="button"
                type="warning"
                >{{
                  countdown >= 60 ? "发送验证码" : `${countdown}秒`
                }}</van-button
              >
            </template>
          </van-field>
          <div class="buttonsub">
            <van-button round block type="info" native-type="submit"
              >快速登录</van-button
            >
          </div>
        </van-form>
        <div class="registershow">
          <span>没有账号?</span
          ><span class="registerspan" @click="ifshowing = !ifshowing"
            >立即注册</span
          >
        </div>
      </van-tab>
    </van-tabs>
    <div class="register" v-show="!ifshowing">
      <h5 class="registerh2">注册账号</h5>
      <van-form @submit="doRegister">
        <van-field
          left-icon="phone-o"
          v-model="mobile"
          name="account"
          placeholder="请填写手机号"
          :rules="[
            { required: true, message: '请填写手机号' },
            { pattern: /^1[345678]\d{9}$/, message: '手机号码格式错误' },
          ]"
        />

        <van-field
          v-model="password"
          type="password"
          left-icon="lock"
          name="password"
          placeholder="请填写密码"
          :rules="[
            { required: true, message: '请填写密码' },
            { pattern: /^\w{5,18}$/, message: '密码由5-18数字a-zA-Z组成' },
          ]"
        />
        <van-field
          v-model="sms"
          name="sms"
          left-icon="lock"
          placeholder="请输入验证码"
          :rules="[{ required: true, message: '请填写验证码' }]"
        >
          <template #button>
            <van-button
              round
              size="small"
              :disabled="countdown < 60"
              @click.stop="getSms('register')"
              native-type="button"
              type="warning"
              >{{
                countdown >= 60 ? "发送验证码" : `${countdown}秒`
              }}</van-button
            >
          </template>
        </van-field>
        <div class="buttonsub">
          <van-button round block type="info" native-type="submit"
            >注册</van-button
          >
        </div>
      </van-form>
      <div class="registershow">
        <span>已有账号?</span
        ><span class="registerspan" @click="ifshowing = !ifshowing"
          >立即登录</span
        >
      </div>
    </div>
  </div>
</template>
<script>
import { mapMutations, mapActions } from "vuex";
export default {
  data() {
    return {
      account: "",
      password: "",
      mobile: "",
      sms: "",
      smskey: "",
      countdown: 60,
      ifshowing: true,
    };
  },
  created() {
    this.axios.get("/api/verify_code").then((d) => {
      this.smskey = d.data.data.key;
    });
  },
  methods: {
    //注册账号
    doRegister(val) {
      console.log(val);
      this.axios
        .post("/api/register", {
          account: val.account,
          captcha: val.sms,
          password: val.password,
        })
        .then((d) => {
          if (d.data.status == 200) {
            this.$toast({
              message: "注册成功",
              icon: "passed",
            });
          } else {
            //注册失败
            this.$toast(d.data.msg);
          }
        });
    },
    doSmsLogin() {
      //验证码登录
      this.$axios
        .post("/api/login/mobile", {
          phone: this.mobile,
          captcha: this.sms,
        })
        .then((d) => {
          //判断登录结果
          if (d.data.status == 200) {
            let token = d.data.data.token;
            this.saveToken(token);
            this.getLoginInfo();
            this.$toast({
              message: "登录成功",
              icon: "passed",
              onClose: () => {
                this.$router.go(-1);
              },
            });
          } else {
            this.$toast(d.data.msg);
          }
          console.log(d);
        });
    },
    //获得短信
    getSms(event) {
      let mobile = this.mobile;
      this.axios
        .post("api/register/verify", {
          //post数据
          key: this.smskey, //获取短信的key
          phone: mobile, //手机号
          type: event, //短信类型为登录
        })
        .then((d) => {
          if (d.data.status == 200) {
            //如果发送成功,发送按钮禁用,同时倒计时60秒
            let intervalid = setInterval(() => {
              if (this.countdown <= 0) {
                clearInterval(intervalid);
                this.countdown = 61;
              }
              this.countdown--;
            }, 1000);
            this.$toast("验证码发送成功");
          } else {
            this.$toast(d.data.msg);
          }
        });
    },
    doLogin(val) {
      this.axios.post("api/login", val).then((d) => {
        if (d.data.status == 200) {
          //登录成功
          this.saveToken(d.data.data.token); //存储token
          this.getLoginInfo(d.data.data.token); //调用vuex的action获取登录信息
          //标记
          let timeing = new Date(d.data.data.expires_time).getTime();
          this.saveTimeent(timeing)
          //标记
          this.$toast({
            message: "登录成功",
            icon: "passed",
            onClose: () => {
              this.$router.go(-1);
            },
          });
        } else {
          //登录失败
          this.$toast(d.data.msg);
        }
      });
    },
    ...mapMutations(["saveToken","saveTimeent"]),
    ...mapActions(["getLoginInfo"]),
  },
};
</script>
<style lang="less">
.login {
  height: 100vh;
  position: absolute;
  z-index: 9;
  background-color: #fff;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#eb5447),
    to(#ff8e3b)
  );
  background-image: linear-gradient(180deg, #eb5447 0, #ff8e3b);
  background-image: -moz-linear-gradient(to bottom, #eb5447 0, #ff8e3b 100%);
  width: 100%;
  height: 100vh;
  .shading {
    padding-top: 35px;
    height: 106px;
    .pictrue {
      width: 81px;
      margin: 0 auto;
      img {
        width: 81px;
        height: 81px;
      }
    }
  }
  .whiteBg {
    margin: 15px 32px;
    padding: 22px 15px;
    border-radius: 8px;
    box-sizing: border-box;
    background-color: white;
    .van-tabs__wrap {
      .van-tabs__nav--line {
        font-weight: 700;
        .van-tabs__line {
          width: 72px;
          height: 2.5px;
        }
        .van-tab {
          font-weight: 700;
          font-size: 17px;
          .van-tab__text {
            font-weight: 700;
            font-size: 17px;
          }
        }
      }
    }
  }
  .registershow {
    font-size: 15px;
    color: #ccc;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    span {
      line-height: 100%;
    }
    .registerspan {
      color: #fc4141;
    }
  }
  .register {
    margin: 15px 32px;
    padding: 22px 15px;
    border-radius: 8px;
    box-sizing: border-box;
    background-color: white;
    h5 {
      margin: 5px;
      font-size: 17px;
      text-align: center;
    }
  }
  .buttonsub {
    margin: 16px 0;
    .van-button--info {
      color: #fff;
      background: -webkit-gradient(
        linear,
        left top,
        right top,
        from(#f35447),
        to(#ff8e3c)
      );
      background: linear-gradient(90deg, #f35447 0, #ff8e3c);
      background: -moz-linear-gradient(to right, #f35447 0, #ff8e3c 100%);
      border: none;
    }
  }
}
</style>